  font-size: large;
  border: 2px solid #212121;
  border-radius: 8px;
  text-transform: uppercase;
  padding: 10px 15px;
  margin: 5px;
  display: inline-block;
  cursor: pointer;
  -webkit-transition: font-size 0.5s ease, background-color 0.5s ease-out;
